@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
:root{
  --primary: green;
  --secondary: #333;
  --error: #ff4a4a;
}
/* base styles & title */
body{
  font-family: 'Indie Flower', cursive;
  color: #333;
  color: var(--secondary);
  background-color: #e3e3e3;
}
.App{
  max-width: 960px;
  margin: 0 auto;
}
.title h1{
  color: green;
  color: var(--primary);
  font-size: 5rem;
  letter-spacing: 3px;
  text-align: center;
  font-weight: 100;
  border: 10px solid green;

}
.title h2, .title p{
  text-align: center;
}
.title h2{
  font-size: 2.6rem;
}

.sub {
  text-decoration: underline;
}

/* upload form styles */
form{
  margin: 30px auto 10px;
  text-align: center;
}
label input{
  height: 0;
  width: 0;
  opacity: 0;
}
label{
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid green;
  border: 1px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 30px;
  color: var (green);
  color: var (var(--primary));
  font-weight: bold;
  font-size: 24px;
}
label:hover{
  background: green;
  background: var(--primary);
  color: #333;
}
.output{
  height: 60px;
  font-size: 1rem;
  margin: 4rem;
}
.error{
  color: #ff4a4a;
  color: var(--error);
  margin: 4rem;
  font-size: 2rem;
}

/* progress bar styles */
.progress-bar{
  height: 5px;
  background: green;
  background: var(--primary);
  margin-top: 20px;
}

/* image grid styles */
.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;

}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.7;
}
.img-wrap img{
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
}

/* modal styles */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 3px solid white;
}

/* Media Querie */
@media screen and (max-width: 800px){
  .img-grid{
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 25px;
  } 

  .title h1{
  color: green;
  color: var(--primary);
  font-size: 4rem;
  text-align: center;
  letter-spacing: 5px;
  font-weight: normal;
}
.title h2, .title p{
  font-size: 1.5rem;
  text-align: center;
}
.title h2{
  font-size: 1.6rem;
}

.error{
  color: #ff4a4a;
  color: var(--error);
  margin: 4rem;
  font-size: 1rem;
}

}

